/********************   ENTIRE HTML STYLE   *****************************/


* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    
  }

  body {
    background-color: WHITE;
  }
  

  button {
    z-index: 1;
  }

   
  html {
    font-family: "Montserrat", sans-serif;
  }
  
   /********************  FONT  *****************************/

   h1 {
    font-size: 4.0rem;
  }
  
  /********************  NAVIGATION  *****************************/
  #GPS {
    background-color:black; /*whichever you want*/
    opacity: 0.8;
    z-index: 1;
  }

  #GPS a {
    color: white;
    opacity: .6;

  }

  #GPS a:hover {
    color: white;
    opacity: 1;
  }

  .navbar-brand {
    position: relative;
    right: 30px;
    bottom: 3px;
  }

  /* spaces out the items in the nav bar */
  .nav-item {
    padding: 0 25px;
  }
  
  
  .line {
    position: relative;
    top: 725px;
    background-color: white;
    border-style: none;
    height: 25px;
    
  }
  
  
  
  li {
    color: white;
    font-size: 13px;
    opacity: .7;
  }
  
   /********************   FIRST SECTION  *****************************/
   .pageTitle {
    color: black;
    background-color: white;
    text-align: center;
    margin-top: 100px;
    height: 10px;
  }
  

  .pageTitle p {
    opacity: .5;
    margin: 0;
    padding: 0;
    font-size: 1rem;
    
  }
  
  .pageTitle a {
    font-size: 1.2rem;
    padding: 2%;
  }
  
  .iPic {
    position: relative;
    bottom: 155px;
  }
  
  header h1 {
    color: #fff;
    font-family: "McLaren", cursive;
    font-weight: 200;
  }
  
  /********************   BUY SECTION  *****************************/
  .second {
    background-color: black;
    position: relative;
    top: 700px;
    padding: 15%;
    text-align: center;
    color: white;
  }
  
  /* SECOND DIV H1 */
  .sech1 {
    position: relative;
    color: white;
    bottom: 234px;
  }

  .fadedPic {
    position: absolute;
    left: 12%;
    top: -35%;
  }
  
  /********************   SPECS SECTION   *****************************/
  .third {
    background-color: #f9f9f9;
    position: relative;
    top: 650px;
    text-align: center;
  }
  
  .thirdText {
    max-width: 400px;
    position: absolute;
    left: 1020px;
    text-align: right;
    margin-top: 20px;
  }


  /********************   FOURTH SECTION   *****************************/
  

  .options {
      position: relative;
      top: 750px;
      left: 250px;
      background-color: transparent;
      border-radius: 25px;
      padding: 25px 15px;
      height: 500px;
      text-align: center;
      width: 200px;
      
  }

  .options h2, h4, h5 {
      color: black;
      text-align: center;
  }

  .container {
      columns: 2;
  }
  
  .smPhone {
    height: 35%;
    width: 110%;
    position: relative;
    
  }

  /********************   FOOTER  *****************************/
  footer {
    position: absolute;
    text-align: center;
    /* text appears in the middle of the page so -300 drops the page lower */
    bottom: -3000px;
    width: 100%;
    height: 2.5rem;
  }
  
  footer p, h4 {
    color: #ccc;
  }
  
 
  
  
  
  